import React from 'react'

import Slider from 'react-slick'

import 'slick-carousel/slick/slick.css'
import 'slick-carousel/slick/slick-theme.css'

import { ICSPlans } from '@/core/data/plans'

import PlanBox from './PlanBox'

const ICSPlanComponent = () => {
  var settings = {
    dots: true,
    infinite: false,
    slidesToShow: 1,
    centerMode: true,
    centerPadding: '10px',
    arrows: false,
  }
  return (
    <Slider {...settings}>
      {ICSPlans.map(plan => (
        <div key={plan.description}>
          <PlanBox
            title="Ituran com Seguro"
            subtitle={plan.description}
            features={plan.features}
            includedFeatures={plan.includedFeatures}
          />
        </div>
      ))}
    </Slider>
  )
}

export default ICSPlanComponent
