import React from 'react'

import {
  Box,
  Divider,
  ListItem,
  Text,
  UnorderedList,
  VStack,
} from '@chakra-ui/react'

type PlanBoxProps = {
  title: string
  subtitle?: string
  features?: string[]
  includedFeatures?: string[]
}

const PlanBox = ({
  title,
  subtitle,
  features,
  includedFeatures,
}: PlanBoxProps) => {
  return (
    <>
      <Box paddingX="6" paddingY="5" shadow="xl" height="72" width="56" mr="8">
        <VStack as="header" textAlign="center" marginBottom="4">
          <Text as="h3" color="blue.light" fontSize="sm" fontWeight="bold">
            {title}
            {subtitle && (
              <Text
                as="small"
                display="block"
                fontSize="2xs"
                color="gray.regular"
                fontWeight="normal"
              >
                {subtitle}
              </Text>
            )}
          </Text>

          <Divider width="8" height="2px" bg="blue.light" />
        </VStack>

        <UnorderedList lineHeight="1.25rem">
          {features?.map(feature => (
            <ListItem key={feature} color="gray.regular" fontSize="2xs">
              {feature}
            </ListItem>
          ))}
        </UnorderedList>

        {includedFeatures && (
          <Text color="blue.light" fontSize="2xs" fontWeight="bold">
            Incluso no seguro
          </Text>
        )}

        <UnorderedList lineHeight="1.125rem">
          {includedFeatures?.map(feature => (
            <ListItem key={feature} color="gray.regular" fontSize="2xs">
              {feature}
            </ListItem>
          ))}
        </UnorderedList>
      </Box>

      <Box
        borderColor="blue.light"
        borderWidth="1px"
        color="blue.light"
        fontSize="2xs"
        padding="3"
        display="flex"
        alignItems="center"
        justifyContent="center"
        marginY="3.5"
        marginX="auto"
        height="6"
        width="28"
      >
        Instalação Gratuita
      </Box>
    </>
  )
}

export default PlanBox
