import React from 'react'

import Slider from 'react-slick'

import 'slick-carousel/slick/slick.css'
import 'slick-carousel/slick/slick-theme.css'

import { TrackerPlans } from '@/core/data/plans'

import PlanBox from './PlanBox'

const TrackerPlansComponent = () => {
  var settings = {
    dots: true,
    infinite: false,
    slidesToShow: 1,
    centerMode: true,
    centerPadding: '10px',
    arrows: false,
  }
  return (
    <Slider {...settings}>
      {TrackerPlans.map(plan => (
        <div key={plan.title}>
          <PlanBox title={plan.title} features={plan.features} />
        </div>
      ))}
    </Slider>
  )
}

export default TrackerPlansComponent
